
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #6d6d6d;
  width:100%;
  overflow:hidden;
}
*{
   margin:0;padding:0;
}
html,body{
   font-size: 16px;
}
li{
   list-style:none;
}
a{
   text-decoration:none;color: #777;
}
img{
   width:100%;
}
table{
    width:100%;
}



